const classesNameContainer = {
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}

const previewStyle = {
  height: 240,
  width: 320,
}

class QrCodeReader extends Component {
  state = {
    result: 'Point the camera to the QR Code of the work within the marker.'
  }

  validateQrData = data => { //Add more logic
    if (data.includes('https://')) {
      return true
    }
    return false
  }

  handleScan = data => {
    if (data) {
      this.setState({
        result: data
      })
    }
    if (!this.validateQrData(data)) {
      return
    }
    // console.log('aqui')
    const msg = `Link Identified: <a href=${data} target="_blank" style="text-decoration:none">Open Form</a>`
    openSnackbar({ message: msg, variant: 'success' })
    // window.open(
    //   this.state.result,
    //   '_blank' // <- This is what makes it open in a new window.
    // );
    // this.props.history.push('/')
  }
  // handleError = err => {
  // console.error(err)
  //   this.setState({
  // 	  result: 'Erro durante identificação do dispositivo.'
  //   })
  // }
  render() {
    return (
      <Grid container direction="column" justify="space-between" alignContent="center">
        <Grid item sm={8} style={{ width: '100%', maxWidth: 400}}>
          {/* <div ></div> */}
          <QrReader
            delay={500}
            onError={this.props.handleError}
            onScan={this.handleScan}
          />
        </Grid>
        <Grid item sm={4}>
          {/* <p>{this.state.result}</p> */}
          <p>Point the camera to the QR Code of the work within the marker.</p>
        </Grid>
        <Button onClick={e => this.handleScan('https://goo.gl/DLsRGQ')}>Testando</Button>
        {/* <Notifier duration={20000}></Notifier> */}
      </Grid>
    )
  }
}
export default QrCodeReader
